</div>\r\n              </td>\r\n            </tr>\r\n          </table>\r\n        </div>\r\n        <div class=\"modal-footer\">\r\n          <button type=\"button\" class=\"btn btn-secondary\" @click=\"closeModal\">Cerrar</button>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport { Modal } from 'bootstrap';\r\n\r\nexport default {\r\n  props: ['firmantes'],\r\n  methods: {\r\n    closeModal() {\r\n        $('#firmantesModal').modal('hide'); \r\n    }\r\n  },\r\n};\r\n</script>\r\n\r\n\r\n\r\n<style scoped>\r\n.modal {\r\n    position: absolute!important;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
