ntos y firmas que necesites.'\r\n          },\r\n          {\r\n            image: 'img/lex/bg1.png',\r\n            alt: 'Documento privado con firma avanzada',\r\n            title: 'Paso 2',\r\n            sub1: 'Paga segur@ con Transbank',\r\n            sub2: 'TODOS los documentos y firmas que necesites.'\r\n          }\r\n        ],\r\n        categoriasDocumentos: [],\r\n        steps: [\r\n          { number: 1, icon: 'description', title: 'Elige y completa tu documento' },\r\n          { number: 2, icon: 'group_add', title: 'Aade los firmantes' },\r\n          { number: 3, icon: 'credit_card', title: 'Realiza el pago con Transbank' },\r\n          { number: 4, icon: 'edit_note', title: 'Firma tu documento online' }\r\n        ]\r\n      };\r\n    },\r\n    methods: {\r\n      // Puedes agregar tus mtodos aqu si es necesario\r\n    }\r\n  };\r\n</script>\r\n\r\n<style scoped>\r\n\r\n@keyframes showTopText {\r\n        0% {\r\n            transform: translate3d(0, 100%, 0);\r\n        }\r\n\r\n        40%,\r\n        60% {\r\n            transform: translate3d(0, 50%, 0);\r\n        }\r\n\r\n        100% {\r\n            transform: translate3d(0, 0, 0);\r\n        }\r\n    }\r\n\r\n    @keyframes showBottomText {\r\n        0% {\r\n            transform: translate3d(0, -100%, 0);\r\n        }\r\n\r\n        100% {\r\n            transform: translate3d(0, 0, 0);\r\n        }\r\n    }\r\n\r\n    .animated-title {\r\n        line-height: normal;\r\n        color: #222;\r\n        height: 134vmin;\r\n        left: 70%;\r\n        position: absolute;\r\n        top: 21%;\r\n        transform: translate(-64%, -54%);\r\n        width: 72vmin;\r\n    }\r\n\r\n    .animated-title>div {\r\n        height: 49.6%;\r\n        overflow: hidden;\r\n        position: absolute;\r\n        width: 100%;\r\n    }\r\n\r\n    .animated-title>div div {\r\n        font-size: 2.6vmin;\r\n        padding: 3.4vmin 0;\r\n        position: absolute;\r\n    }\r\n\r\n    .animated-title>di